<!--  -->
<template>
  <div class="">
    <div class="top">
      <el-link
        type="success"
        href="https://wcity.oss-cn-hangzhou.aliyuncs.com/read.xls"
        >点击查看上传模板</el-link
      >

      <el-upload
        class="upload-demo"
        :on-success="uploadSuccess"
        action="http://175.178.189.129:11000/uploadFile/teacherExcel"
        :headers="uploadHeaders"
      >
        <el-button size="small" type="success" class="changeImage"
          >上传老师信息</el-button
        >
      </el-upload>
    </div>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      style="width: 100%"
    >
      <el-table-column
        type="selection"
        header-align="center"
        align="center"
        width="50"
      >
      </el-table-column>

      <el-table-column
        prop="usreName"
        header-align="center"
        align="center"
        label="用户名"
      >
      </el-table-column>
      <el-table-column
        prop="memberName"
        header-align="center"
        align="center"
        label="名字"
      >
      </el-table-column>
      <el-table-column
        prop="profession"
        header-align="center"
        align="center"
        label="专业"
      >
      </el-table-column>

      <el-table-column
        prop="email"
        header-align="center"
        align="center"
        label="邮箱"
      >
      </el-table-column>

      <el-table-column
        prop="sex"
        label="性别"
        width="100"
        filter-placement="bottom-end"
      >
        <template slot-scope="scope">
          <el-tag disable-transitions>{{
            scope.row.sex == 0 ? "女" : "男"
          }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column
        fixed="right"
        header-align="center"
        align="center"
        width="150"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="deleteHandle(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper"
    >
    </el-pagination>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      uploadHeaders: { token: this.$store.state.user.token },
      token: "",
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataList: [],
      dataForm: {
        key: "",
      },
      dataListSelections: [],
      fileList: [
        {
          name: "food.jpeg",
          url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        },
        {
          name: "food2.jpeg",
          url: "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100",
        },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    //上传文件成功回调
    uploadSuccess(response, file, fileList) {
      console.log(response);
      if (response.code == 0) {
        //添加学生成功
        this.$message({
          message: "恭喜你，添加老师成功",
          type: "success",
        });
        this.getDataList();
      } else {
        this.$message.error("寄！添加学生失败");
      }
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      this.$http
        .get("courseSystem/student/allTeacher/list", {
          params: {
            page: this.pageIndex,
            pageSize: this.pageSize,
            key: this.dataForm.key,
          },
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list;
            this.totalPage = data.page.totalCount;
          } else {
            this.dataList = [];
            this.totalPage = 0;
          }
          this.dataListLoading = false;
        });
    },

    // 多选
    selectionChangeHandle(val) {
      this.dataListSelections = val;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.token = this.$store.state.user.token;
    console.log(this.token);
    this.getDataList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.top {
  margin-bottom: 10px;
  display: flex;
}
.upload-demo {
  margin-left: 20px;
}
</style>